import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import {baseUrl} from '../globals';
@Component({
  selector: 'companies',
  templateUrl: 'companies.component.html',
})
export class CompaniesComponent implements OnInit{
  companies:any=[];
  applied:any=[];
  constructor(private _http:Http) {}
  ngOnInit(){
    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    console.log(baseUrl+'jnf');

    this._http
      .post(baseUrl+'jnf',{"type": "get"}, headers)
        .subscribe(res => {
          var temp = JSON.parse(res['_body'])
          this.companies=temp.companies;
          this.applied=temp.applied;
          console.log(this.companies);
          console.log(this.applied);
          console.log(temp);
        }, error => {
          console.log(error);
        });
  }


  checkIfApplied(name, desig){
    var flag=true;
    if(this.applied[0])
      for(var i=0; i<this.applied.length; i++){
        if(this.applied[i].cmpName==name  && this.applied[i].cmpDesig==desig ){
          flag= false;
          break;
        }
      }
    return flag;
  }


  apply(name, desig){
    console.log({'cmpName': name, 'cmpDesig': desig });
    var headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');

    this._http
      .post(baseUrl+'apply',{'cmpName': name, 'cmpDesig': desig }, headers)
        .subscribe(res => {
          this.applied.push({'cmpName': name, 'cmpDesig': desig });
        }, error => {
          console.log(error);
        });
  }


}



